.App {
	height: 100vh;
	width: 100%;
	display: flex;
	align-items: center;
	flex-direction: column;
	background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
}

.heading {
	font-family: 'Arial', cursive;
	font-size: 20px;
}

.container {
	height: auto;
	width: 800px;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

input {
	padding-left: 5px;
	font-size: 20px;
	height: 36px;
}

.middle,
.right {
	width: 120px;
}

.switch {
	padding: 5px;
	background-color: rgb(226, 252, 184);
	border-radius: 50%;
	cursor: pointer;
}

.result {
	box-sizing: border-box;
	width: 800px;
	padding-left: 30px;
}

.paises {
	box-sizing: border-box;
	width: 800px;
	padding-left: 30px;
  font-size: 9px;
}

button {
	width: 100px;
	height: 30px;
	font-weight: bold;
	font-size: 20px;
	margin-top: 15px;
	border: 2px solid rgb(34, 127, 139);
	background-color: rgb(226, 252, 184);
	cursor: pointer;
}

p,
h3,
button,
.switch {
	color: rgb(34, 34, 139);
}

p {
	font-size: 20px;
}
h3 {
	font-size: 18px;
}
